import React from 'react';

const SearchBar = ({ logosrc, onchange }) => {
	
	return (
		<React.Fragment>
			<nav className="app-navbar">
				{/* <img className="app-logo" src={logosrc} alt="globe" width="50px" height="50px" /> */}
				<h3 className="app-name" style={{ display: 'inline' }}>
					Country Catalog
				</h3>
				<input className="app-searchbar" type="search" placeholder="Type a country to search..." onKeyUp={(e)=>onchange(e)} />
			</nav>
		</React.Fragment>
	);
};

export default SearchBar;
