import './assets/css/App.css';
import logo from './assets/img/globe.jpg';
import SearchBar from './components/SearchBar';
import CountryCard from './components/CountryCard';
import CountryModal from './components/CountryModal';
import { useState } from 'react';

const App = () => {
	const [ countries, setCountries ] = useState([]);
	const [ countryInfo, setCountryInfo ] = useState({});
	const [ isShowModal, setIsShowModal ] = useState(false);

	const fetchCountries = async (e) => {
		if (e.target.value === '') {
			setCountries([]);
		} else {
			let countryRX = new RegExp(`^${e.target.value}`, 'i');
			const response = await fetch('https://restcountries.eu/rest/v2/all');
			const countriesJSON = await response.json();
			const filteredCountries = countriesJSON.filter((country) => countryRX.test(country.name));
			setCountries([ ...filteredCountries ]);
		}
	};

	const displayCountryModal = async (countryName) => {
		const response = await fetch(`https://restcountries.eu/rest/v2/name/${countryName}`);
		const countriesDetails = await response.json();
		setIsShowModal(true);
		setCountryInfo( ...countriesDetails );
		
	};
	const hideCountryModal = () => {
		setIsShowModal(false);
		setCountryInfo({});
	};

	
	return (
		<>
		<div className="app-container">
			<SearchBar logosrc={logo} onchange={fetchCountries} />
			<div className="app-result-container">
				{countries.length > 0 ? (
					countries.map((country) => (
						<CountryCard key={country.numericCode} country={country} onclick={displayCountryModal} />
					))
				) : (
					<p className="app-noresult">No search result found</p>
				)}	
			</div>
			</div>
			{isShowModal  && <CountryModal country={countryInfo} showmodal={{display:'block'}} closemodal={hideCountryModal}/>}
		
			</>
		
	);
};

export default App;
