import React from 'react';

const CountryCard = ({ country, onclick }) => {
	return (
		<div className="country-card" onClick={() => onclick(country.name)} >
			<img src={country.flag} className="country-flag" alt="..." width="100" height="70" />
			<p className="country-name">{country.name.toUpperCase()}</p>
		</div>
	);
};

export default CountryCard;
