const CountryModal = ({ country, showmodal, closemodal }) => {
	const closeModalPopup = () => {
		document.getElementsByClassName('country-modal-popup')[0].style.display = 'none';
		closemodal();
	};
	return (
		<div className="country-modal-popup" style={showmodal}>
			<span className="btn-close" onClick={closeModalPopup}>
				X
			</span>
			<p className="country-modal-name">{country.name}</p>
			<hr />
			<img src={country.flag} className="country-modal-flag" alt="..." width="100" height="70" />
			<div className="country-modal-summary">
				<ul>
					<li>
						<span className="country-modal-label">Capital: </span>
						<span className="country-modal-label-value"> {country.capital}</span>
					</li>
					<li>
						<span className="country-modal-label">Population: </span>
						<span className="country-modal-label-value">{country.population}</span>
					</li>
					<li>
						<span className="country-modal-label">ShortCode: </span>
						<span className="country-modal-label-value"> {country.alpha3Code}</span>
					</li>
				</ul>
			</div>
			<div className="country-modal-desc">
				<p>
					Lorem ipsum dolor sit amet consectetur adipisicing elit. Aut error quas totam voluptatem odio
					officia eaque dolore, esse ex dicta consequatur unde harum nesciunt? Tenetur animi numquam, dolorem
					magni, praesentium vero hic culpa similique rem provident mollitia odit, reprehenderit voluptatem
					ipsum in neque ab nihil aspernatur corporis ipsa quasi laudantium.
				</p>
				<p>
					Lorem ipsum dolor sit amet consectetur adipisicing elit. Vitae eos aliquam facilis eaque vel rem
					repudiandae enim magnam officiis in.
				</p>
			</div>
		</div>
	);
};

export default CountryModal;
